import React, { Component, Dispatch } from 'react';
import { connect } from 'react-redux';
import { createStyles, withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';

import { userStatus } from '../../constants/constants';
import { userActions } from '../../store/actions';
import './MyPage.css';

const styles = (theme: any) => createStyles({
  listItem: {
    padding: theme.spacing(1, 1),
  },
  total: {
    fontWeight: 700,
  },
  title: {
    marginTop: theme.spacing(2),
  },
});

interface Props {
  me: any;
  orders: any;
  onGetMe: () => any;
  onGetOrders: () => any;
  classes: any;
  getMeStatus: string;
}

class MyPage extends Component<Props> {
  componentDidMount() {
    this.props.onGetMe()
      .then(() => {
        this.props.onGetOrders();
      });
  }

  makeBookList(books: any) {
    const { classes } = this.props;
    return books.map((book: any) => (
      <ListItem className={classes.listItem} key={book.id}>
        <ListItemText primary={book.title} secondary={book.subtitle} />
        <Typography variant="body2">
          {book.count}
          
        </Typography>
        <Typography variant="body2">
          &nbsp;*&nbsp;
        </Typography>
        <Typography variant="body2">
          {book.full_price}
          
        </Typography>
      </ListItem>
    ));
  }

  // eslint-disable-next-line class-methods-use-this
  makeBookInfoReviewList(books: any) {
    const bookInfoList = [];
    for (let i = 0; i < books.length; i += 1) {
      const book = books[i];
      let sign = '';
      if (book.sign) {
        sign = book.sign;
      }
      bookInfoList.push(
        <Grid item xs={12} id={`bookinforeview${i}`} key={`bookinforeview${i}`}>
          <Typography variant="body1" gutterBottom>
            - - - - - - - - - - - - - - - - - - - - - - - - - - -
          </Typography>
          <Typography variant="body1" color="primary" gutterBottom>
            {book.title}
            &nbsp;
            [
            {i + 1}
            ]
          </Typography>
          <Typography gutterBottom>
            :&nbsp;
            {book.address}
          </Typography>
          <Typography gutterBottom>
              ( ):&nbsp;
            {book.receiver_phone_number}
          </Typography>
          <Typography gutterBottom>
             ( ):&nbsp;
            {book.receiver_name}
          </Typography>
          <Typography gutterBottom>
             :&nbsp;
            {sign}
          </Typography>
          <Typography gutterBottom />
        </Grid>,
      );
    }
    return bookInfoList;
  }

  makeOrderList(orders: any) {
    const { classes } = this.props;
    return orders.map((order: any) => {
      let bookBasketStatus = '  ';
      let accountInfo = null;
      if (order.status === -1) {
        bookBasketStatus = ' ';
      }
      if (order.status === 2) {
        bookBasketStatus = ' ';
        accountInfo = (
          <>
            <Typography variant="body1" gutterBottom>
                 .
            </Typography>
            <Typography variant="body1" gutterBottom>
               618701-04-129813 ( )
            </Typography>
          </>
        );
      } else if (order.status === 3) {
        bookBasketStatus = ' ';
      } else if (order.status === 4) {
        bookBasketStatus = ' ';
      } else if (order.status === 5) {
        bookBasketStatus = ' ';
      }

      let bookBasketInfo = '';
      let priceInfo = null;
      let orderInfo = null;
      if (order.info === '2020.04.10YEARS') {
        bookBasketInfo = ' 10   ';
        priceInfo = (
          <>
            <ListItem className={classes.listItem}>
              <ListItemText primary="" />
              <Typography variant="subtitle1" className={classes.total}>
                <del>
                  {order.total_price}
                  
                </del>
              </Typography>
            </ListItem>
            <ListItem className={classes.listItem}>
              <ListItemText primary="" />
              <Typography variant="subtitle1" className={classes.total}>
                {order.max_price}
                
              </Typography>
            </ListItem>
          </>
        );

        let postalCode = null;
        if (order.postal_code !== '') {
          postalCode = (
            <Typography gutterBottom>
              :
              {' '}
              {order.postal_code}
            </Typography>
          );
        }
        orderInfo = (
          <>
            <Grid item xs={12} sm={6}>
              <Typography variant="h6" gutterBottom className={classes.title}>
                 
              </Typography>
              <Grid container>
                <Grid item xs={6}>
                  <Typography gutterBottom> </Typography>
                </Grid>
                <Grid item xs={6}>
                  <Typography gutterBottom>
                    {order.family_name}
                    {order.given_name}
                  </Typography>
                </Grid>
              </Grid>
              <Grid container>
                <Grid item xs={6}>
                  <Typography gutterBottom> </Typography>
                </Grid>
                <Grid item xs={6}>
                  <Typography gutterBottom>
                    {order.receiver_family_name}
                    {order.receiver_given_name}
                  </Typography>
                </Grid>
              </Grid>
              <Grid item xs={12}>
                <Typography gutterBottom></Typography>
              </Grid>
              <Grid item xs={12}>
                <Typography gutterBottom>{order.address}</Typography>
                {postalCode}
              </Grid>
            </Grid>
            <Grid item container direction="column" xs={12} sm={6}>
              <Typography variant="h6" gutterBottom className={classes.title}>
                  
              </Typography>
              <Grid container>
                <Grid item xs={6}>
                  <Typography gutterBottom> </Typography>
                </Grid>
                <Grid item xs={6}>
                  <Typography gutterBottom>{order.payer}</Typography>
                </Grid>
              </Grid>
              <Grid container justify="center">
                <Grid item xs={6}>
                  <Typography gutterBottom>
                    {order.email}
                  </Typography>
                </Grid>
              </Grid>
              <Grid container justify="center">
                <Grid item xs={6}>
                  <Typography gutterBottom>
                    {order.phone_number}
                  </Typography>
                </Grid>
              </Grid>
            </Grid>
          </>
        );
      } else if (order.info === '2020.06.NEWBOOK') {
        bookBasketInfo = '      ';
        priceInfo = (
          <ListItem className={classes.listItem}>
            <ListItemText primary="" />
            <Typography variant="subtitle1" className={classes.total}>
              {order.total_price}
              
            </Typography>
          </ListItem>
        );
        orderInfo = (
          <>
            <Grid item xs={12}>
              <Typography variant="h6" gutterBottom className={classes.title}>
                 
              </Typography>
              <Grid container>
                <Grid item xs={6}>
                  <Typography gutterBottom> </Typography>
                </Grid>
                <Grid item xs={6}>
                  <Typography gutterBottom>
                    {order.family_name}
                    {order.given_name}
                  </Typography>
                </Grid>
                <Grid item xs={6}>
                  <Typography gutterBottom>
                    {order.email}
                  </Typography>
                </Grid>
                <Grid item xs={6}>
                  <Typography gutterBottom>
                    {order.phone_number}
                  </Typography>
                </Grid>
              </Grid>
              {this.makeBookInfoReviewList(order.books)}
            </Grid>
            <Grid item container direction="column" xs={12}>
              <Typography variant="h6" gutterBottom className={classes.title}>
                  
              </Typography>
              <Grid container>
                <Grid item xs={6}>
                  <Typography gutterBottom> </Typography>
                </Grid>
                <Grid item xs={6}>
                  <Typography gutterBottom>{order.payer}</Typography>
                </Grid>
              </Grid>
            </Grid>
          </>
        );
      }

      return (
        <div className="OrderElement" key={order.id}>
          <List disablePadding>
            <ListItemText primary=" " />
            <Typography variant="subtitle1" className={classes.total}>
              {order.id}
            </Typography>
            <hr />
            <ListItem className={classes.listItem}>
              <ListItemText primary="" />
              <Typography variant="subtitle1" className={classes.total}>
                {bookBasketInfo}
              </Typography>
            </ListItem>
            <ListItem className={classes.listItem}>
              <ListItemText primary="" />
              <Typography variant="subtitle1" className={classes.total}>
                {bookBasketStatus}
              </Typography>
            </ListItem>
            {accountInfo}
            <hr />
            {this.makeBookList(order.books)}
            <hr />
            {priceInfo}
          </List>
          <hr />
          <Grid container spacing={2}>
            {orderInfo}
          </Grid>
        </div>
      );
    });
  }

  render() {
    const { classes } = this.props;

    let orders = null;
    if (this.props.orders.length > 0) {
      orders = this.makeOrderList(this.props.orders);
    } else {
      orders = (
        <div>
          <hr />
          <Typography variant="h5" gutterBottom>
              .   ?
          </Typography>
        </div>
      );
    }

    let username = '';
    let email = '';
    let name = '';
    if (this.props.getMeStatus === userStatus.FAILURE || this.props.me.anonymous === true) {
      username = '';
      email = '';
      name = '';
    } else {
      username = this.props.me.username;
      email = this.props.me.email;
      name = this.props.me.givenName;
    }

    return (
      <Container component="main" fixed maxWidth="md">
        <div className="ForceMargin" />
        <main className={classes.layout}>
          <Typography variant="h6" gutterBottom>
             
          </Typography>
          <div className="UserInfo">
            <ListItem className={classes.listItem}>
              <ListItemText primary="" />
              <Typography variant="subtitle1" className={classes.total}>
                {username}
              </Typography>
            </ListItem>
            <ListItem className={classes.listItem}>
              <ListItemText primary="" />
              <Typography variant="subtitle1" className={classes.total}>
                {email}
              </Typography>
            </ListItem>
            <ListItem className={classes.listItem}>
              <ListItemText primary="" />
              <Typography variant="subtitle1" className={classes.total}>
                {name}
              </Typography>
            </ListItem>
          </div>
          <Typography variant="h6" gutterBottom>
             
          </Typography>
          {orders}
        </main>
      </Container>
    );
  }
}

const mapStateToProps = (state: any) => ({
  basketStatus: state.user.basketStatus,
  orders: state.user.orders,
  user: state.user.login,
  me: state.user.me,
  getMeStatus: state.user.getMeStatus,
});

const mapDispatchToProps = (dispatch: Dispatch<any>) => ({
  onGetMe: () => dispatch(userActions.getMe()),
  onGetOrders: () => dispatch(userActions.getOrders()),
});

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(MyPage));
